@font-face {
  font-family: "Droid Serif";
  src: url("/fonts/DroidSerif-Bold.woff2") format("woff2"), url("/fonts/DroidSerif-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Droid Serif";
  src: url("/fonts/DroidSerif-BoldItalic.woff2") format("woff2"), url("/fonts/DroidSerif-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap; }

@font-face {
  font-family: "Droid Serif";
  src: url("/fonts/DroidSerif.woff2") format("woff2"), url("/fonts/DroidSerif.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Droid Serif";
  src: url("/fonts/DroidSerif-Italic.woff2") format("woff2"), url("/fonts/DroidSerif-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap; }

html,
body {
  overflow-x: hidden; }

.providers-wrapper {
  position: relative;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 20px;
  line-height: 1.4;
  font-weight: 500; }

.providers-wrapper * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Droid Serif", sans-serif; }

@media (min-width: 800px) {
  .providers-wrapper {
    max-width: 1400px;
    margin: 20px auto;
    box-shadow: 0 5px 10px rgba(0, 255, 128, 0.8); } }

.full-page {
  min-height: 100vh;
  padding: 10vh 40px 20px 40px; }

.block-header {
  font-size: 2em;
  font-weight: 700;
  margin-bottom: 1em; }

.header {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.5)), url(/images/header/header_background.jpg);
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center; }

.header__title {
  color: white;
  text-shadow: 0 0 2px #000;
  font-size: 60px;
  line-height: 1.2em; }

@media (min-width: 600px) {
  .header__title {
    max-width: 760px;
    margin: 0 auto; } }

@media (min-width: 800px) {
  .header {
    clip-path: polygon(0% 0%, 100% 0, 100% 95%, 0% 100%); }
  .header__title {
    font-size: 100px;
    max-width: 1000px; } }

.list-services__services {
  list-style: none; }

.list-services__item {
  background-color: rgba(0, 255, 128, 0.5);
  padding: 10px 20px;
  margin-bottom: 1.2em; }

@media (min-width: 800px) {
  .list-services__header {
    font-size: 60px;
    text-align: center; }
  .list-services__services {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .list-services__item {
    margin: 10px;
    width: 40%; } }

.meme__profit {
  width: 80%;
  margin: 0 auto; }

.meme__list {
  list-style: none; }

.meme__item {
  padding: 0px 10px 0px 10px;
  margin-bottom: 1.5rem;
  position: relative; }
  .meme__item::before {
    display: block;
    position: absolute;
    top: 2px;
    left: -15px;
    content: "";
    width: 20px;
    height: 20px;
    background-image: url(/images/meme/success.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain; }

.meme__memasik {
  background-image: url(/images/meme/baby.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5px;
  width: 80%;
  min-height: 300px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end; }

.meme__header-2 {
  padding: 0;
  margin: 0;
  max-width: 200px;
  font-size: 18px;
  color: white;
  text-shadow: 2px 2px 5px black;
  text-align: right; }

@media (min-width: 900px) {
  .meme {
    display: flex;
    justify-content: center; }
  .meme__profit {
    width: 30%; }
  .meme__memasik {
    width: 60%; }
  .meme__memasik {
    padding: 20px; }
  .meme__header-2 {
    font-size: 3vmax;
    max-width: 500px; } }
